<template>
    <Row>
        <Col span="8"><span>学员姓名: {{ studentDetail.realName }}</span></Col>
        <Col span="8"><span>年级: {{ studentDetail.gradeName }}</span></Col>
        <Col span="8"><span>学员编号: {{ studentDetail.card }}</span></Col>
        <Col span="8"><span>是否参诊: {{ diagnosisResp.isDiagnosisAttend === 1 ? '是' : '否' }}</span></Col>
        <Col span="8"><span>是否报名: {{ diagnosisResp.isRegist === 1 ? '是' : '否' }}</span></Col>
        <Col span="8"><span>是否缴费: {{ diagnosisResp.isPay === 1 ? '是' : '否' }}</span></Col>
        <Col style="margin-top: 30px;"><h3 class="title">【跟进记录】</h3></Col>
        <Col style="width: 100%;min-height: 100px;position: relative;">
            <Spin size="large" fix v-if="fllowUpRecords.length <= 0"></Spin>
            <template v-else>
              <Row class="record-item" v-for="(item, index) in fllowUpRecords" :key="index">
                <Col span="12">操作时间: {{ item.operateTime || '--' }}</Col>
                <Col span="12">操作人: {{ item.empName }}</Col>
                <Col span="12">类型: {{ operateTypeObj[item.operateType]}}</Col>
                <Col span="12" v-if="item.operateType === 2">维护任务: {{ item.taskNames }}</Col>
                <Col span="24"><Divider style="margin: 10px 0;background-color: #333;"></Divider></Col>
                <!-- 预约跟进 -->
                <template v-if="item.operateType === 1 || item.operateType === 2">
                  <Col span="12">用户意向: {{ item.stuIntention }}</Col>
                  <Col span="12" v-if="item.operateType === 1">渠道标签: {{ item.channelName }}</Col>
                  <Col span="24"></Col>
                  <Col span="12">预约诊断时间: {{ item.diagnosisPlanTime || '--' }}</Col>
                  <Col span="12">下次跟进时间: {{ item.nextFollowTime || '--' }}</Col>
                  <Col span="8">意向服务中心: {{ item.serviceCenterName || '--' }}</Col>
                  <Col span="8">意向学期: {{ item.termName || '--' }}</Col>
                  <Col span="8">意向学科: {{ item.subjectName || '--' }}</Col>
                  <Col span="12">备注: {{ item.remark || '--' }}</Col>
                </template>
                <!-- 成绩录入 -->
                <template v-else>
                  <Col span="12">参诊时间: {{ diagnosisResp.diagnosisTime }}</Col>
                  <Col span="12">参诊年份: {{ diagnosisResp.year }}</Col>
                  <Col span="6">参诊学期: {{ diagnosisResp.termName || '--' }}</Col>
                  <Col span="6">参诊年级: {{ diagnosisResp.gradeName || '--' }}</Col>
                  <Col span="6">参诊学科: {{ diagnosisResp.subjectName || '--' }}</Col>
                  <Col span="6">参诊成绩: {{ diagnosisResp.levelDegreeName || '--' }}</Col>
                  <Col span="24">诊断报告:
                    <a v-if="diagnosisResp.reportUrl" :href="diagnosisResp.reportUrl" target="_blank">报告连接</a>
                    <span v-else>--</span>
                  </Col>
                </template>
              </Row>
            </template>
        </Col>
        <Col><h3 class="title">【添加维护】</h3></Col>
        <Col style="margin-bottom: 10px;" v-if="taskResp.length">
          维护任务 <Tag color="primary" v-for="(item, index) in taskResp" :key="index">{{ item.taskName}}</Tag>
        </Col>
        <Col>
            <SubscribeDiagnosis
                :operateType="2"
                :diagnosisRecordId="diagnosisRecordId"
                :studentDetail="studentDetail"
                @success="$emit('success')"
                @close="$emit('close')"
                :taskResp="taskResp"></SubscribeDiagnosis>
        </Col>
    </Row>
</template>

<script>
import { queryFollowDetail } from '@/api/diagnosis'
import SubscribeDiagnosis from './subscribeDiagnosis'
export default {
  name: 'DiagnosisRecord',
  components: {
    SubscribeDiagnosis
  },
  props: {
    // cityCode / areaId
    studentDetail: {
      type: Object,
      default: () => {
        return {}
      }
    },
    diagnosisRecordId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      fllowUpRecords: [],
      diagnosisResp: {},
      taskResp: [],
      operateTypeObj: {
        1: '预约诊断',
        2: '跟进维护',
        3: '前台散诊成绩直接录入',
        4: '成绩录入',
        5: '线下报告成绩匹配'
      }
    }
  },
  mounted () {
    this.queryFollowDetail()
  },
  methods: {
    refresh () {
      this.queryFollowDetail()
    },
    queryFollowDetail () {
      const params = { id: this.diagnosisRecordId, cityCode: this.studentDetail.cityCode }
      queryFollowDetail(params).then(res => {
        this.fllowUpRecords = Object.freeze(res.followList)
        this.diagnosisResp = Object.freeze(res.diagnosisResp)
        this.taskResp = Object.freeze(res.taskResp)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.title {
  margin-bottom: 15px;
}
.record-item {
  border: 1px solid #333;
  border-radius: 6px;
  padding: 15px;
  margin: 15px 0;
}

.divder {
  // height: 1px;
  width: 100%;
  background: #333;
}
</style>
