import Cookie from 'js-cookie'
export default {
  data () {
    return {
      teacherDetail: {
        cityCode: '',
        id: '',
        type: '',
        empNo: '',
        show: false
      }
    }
  },
  mounted () {
    this.teacherDetail.empNo = JSON.parse(Cookie.get('columbusUserInfo')).id
  },
  methods: {
    showTeacherDetail (cityId, teacherId, teacherType) {
      this.teacherDetail.show = true
      this.teacherDetail.cityCode = cityId
      this.teacherDetail.id = teacherId
      this.teacherDetail.type = teacherType || '0'
      this.$nextTick(() => {
        this.$refs.teacherRef.show()
      })
    }
  }
}
