import axios from '../libs/api.request'

/**
 * 任务工作台-顾问招新数据 @何娇娇
 * http://apidoc.speiyou.cn/api/view/19666#
 */
export const getConsultTargetPank = (params) => {
  return axios.request({
    url: '/targetTask/getConsultTargetPank',
    params: params,
    method: 'get'
  })
}

