/**
 * 招新目标管理-下载模板文件 @何娇娇
 * http://apidoc.speiyou.cn/api/view/19625#
 */
export const downloadExcel = (params) => {
  return axios.request({
    url: '/targetTask/downloadExcel',
    params,
    method: 'get',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    responseType: 'blob'
  })
}
