// import ClassDetail from '../modal/searchClassModal/classDetails'

export default {
  // components: { ClassDetail },
  data () {
    return {
      classDetail: {
        id: '',
        show: false
      }
    }
  },
  methods: {
    showClassDetail (classId) {
      this.classDetail.id = classId
      this.$nextTick(() => {
        this.classDetail.show = true
      })
    },
    // 点击上课时间，展示所有课次嘻嘻
    async showSchooltime (classInfo) {
      const errMsg = await this.$refs.talSchooltime.seeDetail(classInfo)
      if (errMsg) {
        this.$Message.error({ content: `获取上课时间失败: ${errMsg}` })
      }
    }
  }
}
