<template>
  <Row>
    <Form>
      <Col span="10">
        <FormItem label="用户意向" required>
          <Select
            style="width: 130px"
            v-model="formValidate.stuIntention"
            placeholder="请选择"
            >
            <Option
                :value="item"
                v-for="(item, index) in conditions.stuIntention"
                :key="index"
            >{{ item }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="24" v-if="operateType === 1">
        <FormItem label="渠道标签" required>
          <Select
            v-model="formValidate.channelId"
            style="width: 120px;"
            placeholder="请选择"
            clearable
            label-in-value
            >
            <Option
              v-for="(item,index) in diagnosisChannels"
              :value="item.id"
              :key="index"
              >{{ item.name }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="24" v-if="operateType === 1">
        <FormItem label="预约诊断时间" required>
          <DatePicker
              type="datetime" placeholder="请选择" style="width: 200px"
              format="yyyy-MM-dd HH:mm:ss"
              v-model="formValidate.diagnosisPlanTimeVal"
              :options="dateOptions"
              @on-change="dateChange($event, 'diagnosisPlanTime')"></DatePicker>
        </FormItem>
      </Col>
      <Col :span="operateType === 1 ? 24 : 14">
        <FormItem label="下次跟进时间">
          <DatePicker
              type="datetime" placeholder="请选择" style="width: 200px"
              format="yyyy-MM-dd HH:mm:ss"
              v-model="formValidate.nextFollowTimeVal"
              :options="dateOptions"
              @on-change="dateChange($event, 'nextFollowTime')"></DatePicker>
        </FormItem>
      </Col>
      <Col span="8">
        <FormItem label="意向服务中心">
          <Select
            style="width: 120px;"
            v-model="formValidate.serviceCenterId"
            placeholder="请选择"
            @on-change="changeService"
            clearable
            filterable
            label-in-value
            >
            <Option
                v-for="(item,index) in serviceList"
                :value="item.serviceCenterId"
                :key="index"
            >{{ item.deptName }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="8">
        <FormItem label="意向学期">
          <Select
            v-model="formValidate.termId"
            style="width: 120px;"
            placeholder="请选择"
            @on-change="changeTerm"
            clearable
            label-in-value
            >
            <Option v-for="(item,index) in termList" :value="item.id" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
      </Col>
      <Col span="8">
        <FormItem label="意向学科">
          <Select
            v-model="formValidate.subjectId"
            style="width: 220px;"
            :style="{'width': operateType === 1 ? '250px' : '120px' }"
            placeholder="请选择"
            @on-change="changeSubject"
            clearable
            label-in-value
            :multiple="operateType === 1"
            >
            <Option
              v-for="(item,index) in subjectList"
              :value="item.id"
              :key="index"
              >{{ item.name }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="24">
        <FormItem label="备注" required>
          <Input :autosize="{minRows: 3}" :maxlength="500" v-model.trim="formValidate.remark" type="textarea"  placeholder="请记录和用户沟通的结论，便于后续维护中可持续查看。" />
        </FormItem>
      </Col>
    </Form>
    <Col span="24" style="text-align: center;">
        <Button type="primary" style="margin-right: 30px;" :loading="isLoading" @click="submit">提交</Button>
        <Button @click="cancel">取消</Button>
      </Col>
  </Row>
</template>

<script>
import { getLinkage } from '@/api/bd'
import { addPlanDiagnosis, queryServiceByAreaCode, condition, queryDiagnosisChannels } from '@/api/diagnosis'
import Cookie from 'js-cookie'
import { dateFormat } from '../../../utils/utils'
export default {
  name: 'SubscribeDiagnosis',
  props: {
    studentDetail: {
      type: Object,
      default: () => {
        // cityCode / areaId
        return {}
      }
    },
    operateType: {
      type: Number,
      default: 1 // (1预约,2跟进)
    },
    diagnosisRecordId: {
      type: String,
      default: ''
    },
    // 需要维护的任务
    taskResp: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      formValidate: {
        diagnosisRecordId: '', // 预约诊断id(跟进必填,预约非填)
        stuIntention: '', // 学员意向
        diagnosisPlanTime: dateFormat(new Date()), // 预约诊断时间(预约诊断必填)
        diagnosisPlanTimeVal: dateFormat(new Date()),
        nextFollowTime: '', // 下次跟进时间
        nextFollowTimeVal: '',
        serviceCenterId: '', // 意向服务中心id
        serviceCenterName: '', // 意向服务中心
        termId: '', // 意向学期id
        termName: '', // 意向学期
        subjectId: '', // 意向学科id
        subjectName: '', // 意向学科
        remark: '', // 备注
        operateType: '', // 操作类型(1预约,2跟进)
        stuCode: '', // 学员编号
        pyUid: '', // 学员编号
        stuRealName: '', //  学员真实姓名
        empNo: '', //  操作人员工编号
        empName: '' //  操作人员工
      },
      classList: [],
      serviceList: [],
      termList: [],
      subjectList: [],
      isLoading: false,
      conditions: {
        stuIntention: []
      },
      dateOptions: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      diagnosisChannels: [] // 渠道标签
    }
  },
  mounted () {
    this.initView()
    this.formValidate.pyUid = this.studentDetail.uid
    this.formValidate.stuRealName = this.studentDetail.realName
    this.formValidate.stuCode = this.studentDetail.card
  },
  methods: {
    // 渠道标签列表
    queryDiagnosisChannels () {
      queryDiagnosisChannels({}).then(res => {
        this.diagnosisChannels = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    getCondition () {
      condition({}).then(res => {
        this.conditions = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    dateChange (val, typeName) {
      this.formValidate[typeName] = val
    },
    submit () {
      const params = Object.assign({}, this.formValidate, {
        cityCode: this.studentDetail.cityCode,
        operateType: this.operateType,
        empNo: JSON.parse(Cookie.get('columbusUserInfo')).id,
        empName: decodeURI(JSON.parse(Cookie.get('columbusUserInfo')).name),
        diagnosisRecordId: this.diagnosisRecordId || undefined,
        taskResp: this.taskResp || []
      })

      if (!this.formValidate.stuIntention) {
        return this.$Message.warning({ content: '请选择用户意向' })
      }

      if (this.operateType === 1 && !this.formValidate.diagnosisPlanTime) {
        return this.$Message.warning({ content: '请选择预约诊断时间' })
      }

      if (this.operateType === 1 && !this.formValidate.channelId) {
        return this.$Message.warning({ content: '请选择渠道标签' })
      }

      if (!this.formValidate.remark) {
        return this.$Message.warning({ content: '请填写备注' })
      }

      this.isLoading = true
      addPlanDiagnosis(params).then(res => {
        this.$Message.info({ content: '提交成功' })
        this.$emit('success')
        this.formValidate.subjectId = ''
        this.formValidate.stuIntention = ''
        this.formValidate.nextFollowTime = ''
        this.formValidate.nextFollowTimeVal = ''
        this.formValidate.serviceCenterId = ''
        this.formValidate.serviceCenterName = ''
        this.formValidate.termId = ''
        this.formValidate.termName = ''
        this.formValidate.subjectId = ''
        this.formValidate.remark = ''
        this.formValidate.termName = ''
        this.formValidate.termName = ''
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      }).finally(() => {
        this.isLoading = false
      })
    },
    cancel () {
      this.$emit('close')
    },
    initView () {
      this.getServiceCenterListHanlder()
      this.getTermListHanlder()
      this.getCondition()
      this.queryDiagnosisChannels()
    },
    //   学期服务中心
    changeService (data) {
      if (!data) {
        this.formValidate.serviceCenterName = ''
        return
      }
      this.formValidate.serviceCenterName = data.label
    },
    //   学期改变
    changeTerm (data) {
      this.formValidate.subjectId = ''
      this.subjectList = []
      if (!data) {
        this.formValidate.termName = ''
        return
      }
      this.formValidate.termName = data.label

      this.getSubjectListHanlder()
    },
    //   意向学科改变
    changeSubject (data) {
      if (!data || (data && data.length === 0)) {
        this.formValidate.subjectReqList = []
        return
      }

      if (Array.isArray(data)) {
        this.formValidate.subjectReqList = data.map(item => {
          return {
            subjectId: item.value,
            subjectName: item.label
          }
        })
      } else {
        this.formValidate.subjectReqList = [
          {
            subjectName: data.label,
            subjectId: data.value
          }
        ]
      }
    },
    //   获取学科
    getSubjectListHanlder () {
      let baseSubject = {
        cityCode: this.studentDetail.cityCode,
        termIds: this.studentDetail.termId,
        queryType: 's'
      }
      getLinkage(baseSubject)
        .then(res => {
          this.subjectList = res
        })
        .catch(err => {
          this.$Message.error('获取学科失败:' + err.message)
        })
    },
    //   获取学期
    getTermListHanlder () {
      let baseTerm = {
        cityCode: this.studentDetail.cityCode,
        queryType: 't'
      }
      getLinkage(baseTerm)
        .then(res => {
          this.termList = res
        })
        .catch(err => {
          this.$Message.error('获取学期失败:' + err.message)
        })
    },
    // 获取服务中心
    getServiceCenterListHanlder () {
      let ser = {
        cityCode: this.studentDetail.cityCode,
        areaCode: this.studentDetail.areaCode
      }

      queryServiceByAreaCode(ser)
        .then(res => {
          this.serviceList = res
          this.serviceList.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })
        })
        .catch(err => {
          this.$Message.error('获取服务中心失败:' + err.message)
        })
    }
  }
}
</script>
<style lang="less" scoped>
